@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@100;200;300;400;500;600;700;800;900&display=swap);
*{margin:0;padding:0}body{font-family:"Kanit",sans-serif;color:#9ad3bc;display:flex;flex-direction:column;align-items:center;text-align:center}h1{margin:2rem 0;font-weight:400}main{display:flex;flex-direction:column;border:1px solid #9ad3bc;align-items:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}section{display:flex;width:70rem;margin-bottom:2rem}.button{border:1px solid #edc988;text-transform:uppercase;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:.3rem 1.5rem;background-color:#9ad3bc;border-color:#9ad3bc;color:#132743;font-weight:500;font-family:"Kanit",sans-serif;border-radius:.25rem;font-size:1rem;margin-right:.25rem}.button:hover{background-color:#2ec1ac;border-color:#2ec1ac;cursor:pointer}.button:focus{outline:none;border-color:#2ec1ac;background-color:#2ec1ac;color:#eeecda}.button.addTask{font-size:3rem;padding:0 0 .4rem 0;border-radius:50%;width:2.5rem;height:2.5rem;line-height:1rem;margin-top:2rem}.button.moveTask{font-size:1.5rem;padding:.5rem .5rem;margin-bottom:0;line-height:1rem}.button.delete{background-color:#ffa5a5;border-color:#ffa5a5}.button.delete:hover{background-color:#ff8282;border-color:#ff8282}@media only screen and (max-width: 75rem){section{width:100%;flex-direction:column}}
.navbar {
    background-color: #060b26;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  .menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
  }
  
  .nav-menu {
    background-color: #060b26;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
  }
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
  }
  
  .nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
  }
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
  }
  
  .nav-text a:hover {
    background-color: #1a83ff;
  }
  
  .nav-menu-items {
    width: 100%;
  }
  
  .navbar-toggle {
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  span {
    margin-left: 16px;
  }
.statusLine{width:100%;margin:0 .5rem}.statusLine h3{font-weight:500;margin:1rem 0}@media only screen and (max-width: 75rem){.statusLine{width:auto;width:initial}}
.task{padding:1rem;margin-bottom:.5rem;display:flex;align-items:flex-end;border:1px solid #eeecda;border-radius:.25rem}.task.collapsedTask .moveTask{margin-bottom:.5rem;font-size:1.5rem;padding:.3rem .5rem}.task form{display:flex;flex-direction:column;text-align:center;align-items:center;margin:0 .25rem}.task form .input{background-color:transparent;border:1px solid transparent;border-radius:.25rem;color:#eeecda;text-align:center;resize:none;display:flex;font-family:"Kanit",sans-serif}.task form .input:hover{cursor:pointer;border:1px solid #eeecda}.task form .input:focus{outline:none;border-color:#edc988;color:#edc988}.task form .input::-webkit-input-placeholder{color:#eeecda}.task form .input:-ms-input-placeholder{color:#eeecda}.task form .input::-ms-input-placeholder{color:#eeecda}.task form .input::placeholder{color:#eeecda}.task form .title{margin-bottom:.5rem;padding:.5rem 0;width:100%;font-size:1.1rem}.task form .title:hover{cursor:text}.task form .description{margin-bottom:.5rem;padding:.5rem;width:100%;font-size:1.1rem}.task form .description:hover{cursor:text}.task form .urgencyLabels{margin-bottom:1.5rem;display:flex;flex-direction:row}.task form .urgencyLabels label{color:#132743;padding:.05rem .5rem;margin:0 .25rem;text-transform:uppercase;border-radius:.25rem}.task form .urgencyLabels label .icon{font-size:1.25rem;margin-right:.25rem;transition:all .4s ease-in-out}.task form .urgencyLabels label .icon.hide{display:none}.task form .urgencyLabels label .icon.show{display:inline-block}.task form .urgencyLabels label input{display:none}.task form .urgencyLabels label.low{background-color:#9ad3bc}.task form .urgencyLabels label.low:hover{cursor:pointer;background-color:#2ec1ac;border-color:#2ec1ac}.task form .urgencyLabels label.medium{background-color:#edf285}.task form .urgencyLabels label.medium:hover{cursor:pointer;background-color:#2ec1ac;border-color:#2ec1ac}.task form .urgencyLabels label.high{background-color:#ffa5a5}.task form .urgencyLabels label.high:hover{cursor:pointer;background-color:#2ec1ac;border-color:#2ec1ac}.task form .urgencyLabels label.selected{font-weight:700}.task form.collapsed{flex-direction:row;align-content:center}.task form.collapsed .title{margin:0}.task form.collapsed .title:hover{border-color:transparent;cursor:default}.task form.collapsed .urgencyLabels{margin-bottom:0;margin-right:1rem}.task form.collapsed .description,.task form.collapsed label{display:none}.task form.collapsed label.selected{display:inline-block;font-weight:500}.task form.collapsed .button{padding:.025rem .5rem}.task form.collapsed .button.moveTask{background-color:red}.task form.collapsed:hover .button{display:inline-block}
*{margin:0;padding:0;box-sizing:border-box}.card{align-items:center;border-radius:6px;box-shadow:0px 4px 8px #eee;border:1px solid #eee;display:flex;flex-direction:column;margin:25px;width:15rem;height:18rem}.card .profile,.name-profile{padding:10px}.btn{position:absolute;top:270px;width:7em;padding:.1em;color:#fff;font-size:17px;outline:0;background-color:orange;border:0;cursor:pointer;border-radius:5px}.name-profile,p{position:relative;bottom:5px;font-weight:normal}p{bottom:10px;font-weight:lighter}.profile{border-radius:90px}
*{box-sizing:border-box;margin:0;padding:0;font-family:"Nunito",sans-serif}button{border:none;outline:none;width:100%;padding:15px 0;color:#fff;font-size:16px;letter-spacing:1px;background:#603bbb;cursor:pointer}.login{width:100%;min-height:100vh;padding:0 20px;background:#e9e9e9;display:flex}.login .loginContainer{padding:60px;margin:auto;width:100%;max-width:520px;min-height:600px;display:flex;flex-direction:column;justify-content:center;background:radial-gradient(ellipse at left bottom, #16182f 0%, rgba(38, 20, 72, 0.9) 59%, rgba(17, 27, 75, 0.9) 100%);box-shadow:0 50px 70px -20px rgba(0,0,0,.8)}.login .loginContainer label{color:#fff;margin:14px 0;display:block;font-size:22px;line-height:1}.login .loginContainer input{width:100%;border:none;outline:none;font-size:19px;padding:10px;background:rgba(255,255,255,.1);color:#fff;letter-spacing:1px}.login .loginContainer .btnContainer{width:100%;padding:24px 0}.login .loginContainer .btnContainer p{margin:14px 0 0 0;text-align:right;color:#fff}.login .loginContainer .btnContainer p span{color:#ff0;font-weight:500;letter-spacing:.5px;margin-left:5px;cursor:pointer;transition:all 400ms ease-in-out}.login .loginContainer .btnContainer p span:hover{color:red}.login .loginContainer .errorMsg{color:red;font-size:16px}
